import { Search } from '@material-ui/icons';
import React from 'react';
import SearchIcon from '@material-ui/icons/Search';
import { Link } from 'react-router-dom';
function Header() {
  return (
    <div>
      <div className="container-fluid" style={{ width: '100%' }}>
        <nav className="navbar navbar-expand-lg navbar-light bg-light">
          <div className="container-fluid">
            <Link to="/course" className="navbar-brand">
              Udemy
            </Link>
            <Link to="#" className="navbar-brand">
             <img src="data:image/svg+xml;base64," />
            </Link>

            <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
              <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse" id="navbarSupportedContent">
              <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                <li className="nav-item dropdown">
                  <a className="nav-link dropdown-toggle btn-outline-primary  mx-3 b" href="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                    Explore
                  </a>
                  <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
                    <li>
                      <Link to="/about" className="dropdown-item">
                        About
                      </Link>
                    </li>
                    {/* <li>
                      <a className="dropdown-item" href="#">
                        Another action
                      </a>
                    </li> */}
                    <li>
                      <hr className="dropdown-divider" />
                    </li>
                    {/* <li>
                      <a className="dropdown-item" href="#">
                        Something else here
                      </a>
                    </li> */}
                  </ul>
                </li>
                <form className="d-flex">
                  <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search"></input>
                  <SearchIcon style={{ fontSize: 35, borderRadius: 5, color: 'GrayText', marginTop: '3px' }} />
                </form>
              </ul>
              {/* #a54a85 login */}
              <form className="d-flex mx-3">
                <button className="btn btn-outline-secondary mx-3" type="submit">
                  <Link to="/login">Login</Link>
                </button>
                <button className="btn btn-secondary " type="submit" style={{ backgroundColor: '#0090cf' }}>
                  Join
                </button>
              </form>
            </div>
          </div>
        </nav>
      </div>
    </div>
  );
}

export default Header;
