import React, { useEffect, useState } from 'react';
import firebaseApp from '../firebase';
import { useStateValue } from '../StateProvider';
import './TeacherDashboard/css/TeacherCourse.css';
import { Button, Avatar } from '@material-ui/core';

export default function TeacherCourse() {
  const [{ user }, dispatch] = useStateValue();
  const [itemList, setItemList] = useState([]);
  // const [title, setTitle] = useState('');
  // const [id, setId] = useState('');
  // !CHECK  OPERATION
  useEffect(() => {
    const ref = firebaseApp.database().ref('teacher');
    ref.on('value', (snapshot) => {
      if (user) {
        const course = snapshot.child(user.uid).child('course').child('item').val();

        const itemList = [];
        for (let id in course) {
          itemList.push(course[id]);
        }
        // console.log('this is courses ', itemList);
        setItemList(itemList);
      }
      // else {
      //   alert('Login first');
      // }
    });
  });

  return (
    <div className="teacherCourse">
      <h2 className="teacherCourse__header">My courses</h2>
      <div className="teacherCourse__container">
        {itemList.map((item) => {
          const { title, price, image, id, category } = item;
          return (
            <div className="teacherCourse__card">
              <img className="teacherCourse__img" src={image} />
              <div className="teacherCourse__textContainer">
                <p className="teacherCourse__title">{title}</p>

                <div className="teacherCourse__bottom">
                  <p className="teacherCourse__price">
                    <small>₹</small>
                    <strong>{price}</strong>
                  </p>

                  <Button
                    className="teacherCourse__button"
                    onClick={() => {
                      try {
                      } catch (e) {
                        alert('We cant able to delete this course at this moment');
                      }
                      if (user) {
                        let ID = id;

                        const deleteFromTeacher = firebaseApp.database().ref('teacher');
                        const courseRef = firebaseApp.database().ref('course');
                        const deleteFromCategory = firebaseApp.database().ref('category');

                        deleteFromCategory.child(category).child(ID).remove();
                        alert(`${category} and {ID}`);
                        deleteFromTeacher.child(user.uid).child('course').child('item').child(ID).remove();
                        courseRef.child('item').child(ID).remove();
                      }
                    }}
                  >
                    delete
                  </Button>
                </div>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}
{
  /* <div className="card">
   <img className="img" src={image} />
   
   <div className="right__container">
        <div class="test_info">
             <h4>
                  <b>{title}</b>
             </h4>
   
             <p className="product__price">
                  <small>₹</small>
                  <strong>{price}</strong>
             </p>
             <p>ID : {id}</p>
        </div>
   </div>
   </div> */
}

