import React from 'react';
// import '../Product.css';

// import Card from './Card';
import { useStateValue } from '../StateProvider';

import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
//   !style
const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
  cardGrid: {
    //     display: 'flex',
    //     justifyContent: 'center',
    //     alignItems: 'center',
  },
}));

function Product({ id, title, image, price, rating }) {
  const classes = useStyles();
  const [{ basket }, dispatch] = useStateValue();
  // console.log(basket);

  const addToBasket = () => {
    dispatch({
      type: 'ADD_TO_BASKET',
      item: {
        id: id,
        title: title,
        image: image,
        price: price,
        rating: rating,
      },
    });
  };
  const addToWishList = () => {
    dispatch({
      type: 'ADD_TO_WISHLIST',
      item: {
        id: id,
        title: title,
        image: image,
        price: price,
        rating: rating,
      },
    });
  };

  return (
    <div style={{ maxHeight: '400px', margin: '2rem  auto 0px auto', boxShadow: '0 2px 3px 0 rgba(0, 0, 0, 0.2)', backgroundColor: 'white' }}>
      <div style={{ width: '300px', display: 'flex', flexDirection: 'column', alignItems: 'flex-start' }}>
        <div
          key={id}
          style={{
            minHeight: '300px',
          }}
        >
          <img style={{ maxHeight: '400px', width: '300px' }} src={image} alt="" srcset="" />
          <p style={{ margin: '1rem .7rem' }}>{title}</p>

          <div style={{ display: 'flex', justifyContent: 'space-between', margin: '0 .7rem' }}>
            <div>
              <p>
                <small>₹</small>
                <strong>{price}</strong>
              </p>
            </div>
            <div style={{ display: 'flex' }}>
              {Array(rating)
                .fill()
                .map((_, i) => (
                  <p>⭐</p>
                ))}
            </div>
          </div>
        </div>
      </div>
      <div style={{ display: 'flex ', flexWrap: 'wrap', justifyContent: 'space-evenly' }}>
        <button onClick={addToBasket} className="test__button">
          Add to cart
        </button>
        <button className="test__button_wish" onClick={addToWishList}>
          wish List
        </button>
      </div>
    </div>
  );
}

export default Product;
