import React from 'react';
import pic2 from '../assets/island2.jpg';
import Card from './Card';
import Member from './Member';

import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';

import Achiv1 from '../assets/achievement/badge.png';
import Achiv2 from '../assets/achievement/experience.png';
import Achiv3 from '../assets/achievement/star.png';
import Achiv4 from '../assets/achievement/goal.png';
import Achiv5 from '../assets/achievement/goals.png';
import Achiv6 from '../assets/achievement/success.png';
import Achievements from './Achievements';
const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
  cardComponent: {},
}));
function Members() {
  const classes = useStyles();
  return (
    <div>
      <div id="carouselExampleControls" class="carousel slide" data-bs-ride="carousel">
        <div class="carousel-inner">
          <div class="carousel-item active">
            <div>
              <Grid container spacing={1}>
                <Grid item xs={6} style={{ display: 'flex', justifyContent: 'center' }}>
                  <Achievements image={Achiv5} />
                </Grid>
                <Grid item xs={6} style={{ display: 'flex', justifyContent: 'center' }}>
                  <Achievements image={Achiv6} />
                </Grid>
              </Grid>
            </div>
          </div>
          <div class="carousel-item">
            <div>
              <Grid container spacing={1}>
                <Grid item xs={6} style={{ display: 'flex', justifyContent: 'center' }}>
                  <Achievements image={Achiv3} />
                </Grid>
                <Grid item xs={6} style={{ display: 'flex', justifyContent: 'center' }}>
                  <Achievements image={Achiv4} />
                </Grid>
              </Grid>
            </div>
          </div>
          <div class="carousel-item">
            <div>
              <Grid container spacing={1}>
                <Grid item xs={6} style={{ display: 'flex', justifyContent: 'center' }}>
                  <Achievements image={Achiv1} />
                </Grid>
                <Grid item xs={6} style={{ display: 'flex', justifyContent: 'center' }}>
                  <Achievements image={Achiv2} />
                </Grid>
              </Grid>
            </div>
          </div>
        </div>
        <a class="carousel-control-prev" href="#carouselExampleControls" role="button" data-bs-slide="prev">
          <span class="carousel-control-prev-icon" aria-hidden="true"></span>
          <span class="visually-hidden-focusable">Previous</span>
        </a>
        <a class="carousel-control-next" href="#carouselExampleControls" role="button" data-bs-slide="next">
          <span class="carousel-control-next-icon" aria-hidden="true"></span>
          <span class="visually-hidden">Next</span>
        </a>
      </div>
    </div>
  );
}

export default Members;
