import React from 'react';
import '../Product.css';
import { useStateValue } from '../StateProvider';
function WhiteListChild({ id, title, image, price, rating }) {
     const [{ basket }, dispatch] = useStateValue();
     // console.log(basket);

     const addToBasket = () => {
          dispatch({
               type: 'ADD_TO_BASKET',
               item: {
                    id: id,
                    title: title,
                    image: image,
                    price: price,
                    rating: rating,
               },
          });
     };
     const removeFromWishList = () => {
          dispatch({
               type: 'REMOVE_FROM_WISHLIST',
               id: id,
          });
     };
     return (
          <div className="card">
               <img className="img" src={image} />

               <div className="right__container">
                    <div class="test_info">
                         <h5>
                              <b>{title}</b>
                         </h5>

                         <p className="product__price">
                              <small>₹</small>
                              <strong>{price}</strong>
                         </p>
                    </div>
                    <div className="teat__star">
                         {Array(rating)
                              .fill()
                              .map((_, i) => (
                                   <p>⭐</p>
                              ))}
                    </div>
                    <button onClick={addToBasket} className="test__button">
                         Add to cart
                    </button>
                    <button className="test__button_wish" onClick={removeFromWishList}>
                         Remove from wishlist
                    </button>
               </div>
          </div>
     );
}

export default WhiteListChild;
