import React from 'react';
import '../WishList.css';
import ListIcon from '@material-ui/icons/List';
import WhiteListChild from './WhiteListChild';
import { useStateValue } from '../StateProvider';
export default function WishList() {
     const [{ basket, wishlist }, dispatch] = useStateValue();
     return (
          <div>
               <div className="wishlist">
                    <h2>
                         Wish List
                         <span className="wishlist__logo">
                              <ListIcon />
                         </span>
                    </h2>
                    <div className="wishlist__products">
                         {wishlist.map((block) => (
                              <WhiteListChild id={block.id} title={block.title} price={block.price} image={block.image} rating={block.rating} />
                         ))}
                    </div>
               </div>
          </div>
     );
}
