import React from 'react';

function Achievements(props) {
  return (
    <div>
      <div class="card" style={{ width: '18rem', display: 'flex', flexDirection: 'column', border: 'none', margin: '1rem' }}>
        <img class="card-img-top" style={{ width: '20%', margin: 'auto', height: '50px' }} src={props.image} alt="..." />

        <div class="card-body" style={{ borderTop: '1px solid red', marginTop: '1rem' }}>
          <p class="card-text" style={{ textAlign: 'center' }}>
            like business analytics, graphic design, Python, and more
          </p>
        </div>
      </div>
    </div>
  );
}

export default Achievements;
