import React from 'react';
import './css/TeacherDashSideOpt.css';
function TeacherDashSideOpt({ selected, title, Icon }) {
  return (
    <div className={`teacherDashSideOpt ${selected && 'selected'}`}>
      <Icon className="teacherDashSideOpt__icon" />
      <h3 className="teacherDashSideOpt__title"> {title}</h3>
    </div>
  );
}

export default TeacherDashSideOpt;
