import React from 'react';
import { Link, useHistory } from 'react-router-dom';
import firebaseApp from '../firebase';
import '../Payment.css';
import { useStateValue } from '../StateProvider';
import CheckoutProduct from './CheckoutProduct';

// ADD_TO_HISTORY
function Payment() {
     const [{ basket, user, history }, dispatch] = useStateValue();
     // console.log(`THis is payment component  = ${user.uid}`);
     // console.log(`THis is payment component  = ${basket[0].title}`);

     const handelPay = () => {
          // basket.map((basket) => <Order id={basket.id} title={basket.title} image={basket.image} price={basket.price} rating={basket.rating} />);

          const ref = firebaseApp.database().ref('history');
          // console.log(basket);
          if (user) {
               if (basket.length === 0) {
                    alert('please add somthing your basket');
               } else {
                    ref.child(user.uid).child('item').update(basket);
                    // alert('Payment successful');
               }
          } else {
               alert('Please login first');
          }
     };

     return (
          <div className="payment">
               <div className="payment__container">
                    <h1 className="payment__header">
                         checkout(<Link to="/checkout">{basket?.length} item</Link>)
                    </h1>
                    {/* payment section - delivery adderss */}
                    {/* <div className="payment__section">
                         <div className="payment__title">
                              <h3>Delivery Address</h3>
                         </div>
                         <div className="payment__address">
                              <p>{user ? user.email : 'Guest'}</p>
                              
                         </div>
                    </div> */}
                    {/* payment section - Review order */}
                    <div className="payment__section">
                         <div className="payment__title">
                              <h3>Review item :</h3>
                         </div>
                         <div className="payment__items">
                              {basket.map((basket) => (
                                   <CheckoutProduct id={basket.id} title={basket.title} image={basket.image} price={basket.price} rating={basket.rating} />
                              ))}
                         </div>
                    </div>
                    {/* payment section - payment methode */}

                    <div className="payment__section">
                         <div className="payment__title">
                              <h3>Continue for Payment</h3>
                         </div>

                         <button className="payment__payButton" onClick={handelPay}>
                              Enroll
                         </button>
                    </div>
               </div>
          </div>
     );
}

export default Payment;
