import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import BottomNavigation from '@material-ui/core/BottomNavigation';

import ImageAvatars from './ImageAvtar';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
const useStyles = makeStyles({
  root: {
    width: 'auto',
    margin: '1rem',
  },
  typo: {
    // border: '1px solid #000',
    textAlign: 'center',
    marginTop: '1rem',
  },
  container: {
    display: 'flex',
    flexDirection: 'column',
  },
});

export default function SimpleBottomNavigation() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  return (
    <div className={classes.container}>
      <Typography variant="h7" color="initial" className={classes.typo}>
        We collaborate with 200+ leading <span style={{ color: 'blue' }}> universities</span> and <span style={{ color: 'blue' }}>companies </span>:
      </Typography>

      <BottomNavigation
        value={value}
        onChange={(event, newValue) => {
          setValue(newValue);
        }}
        showLabels
        className={classes.root}
      >
        <ImageAvatars />
      </BottomNavigation>
    </div>
  );
}
