import React, { useState } from 'react';
import '../SideBar.css';
import { Button } from '@material-ui/core';
import HomeIcon from '@material-ui/icons/Home';
import WhatshotSharpIcon from '@material-ui/icons/WhatshotSharp';
import SubscriptionsSharpIcon from '@material-ui/icons/SubscriptionsSharp';
import TuneIcon from '@material-ui/icons/Tune';
import AppsIcon from '@material-ui/icons/Apps';
import { Link } from 'react-router-dom';
import SideBarRow from './SideBarRow';
import ForumIcon from '@material-ui/icons/Forum';
function SideBar() {
  const [show, setShow] = useState(false);
  return (
    <div className="sidebar">
      <Button
        className="sidebar__button"
        onClick={() => {
          setShow(!show);
        }}
        variant="outlined"
      >
        Menu
      </Button>
      {show && (
        <div className="icon">
          <Link to="/course" className="sideBar__link">
            <SideBarRow selected={true} Icon={HomeIcon} title={'Course'} />
          </Link>
          <Link to="/category" className="sideBar__link">
            <SideBarRow Icon={AppsIcon} title={'Category'} />
          </Link>
          <Link to="/filter" className="sideBar__link">
            <SideBarRow Icon={TuneIcon} title={'Filter'} />
          </Link>
          <Link to="/community" className="sideBar__link">
            <SideBarRow Icon={ForumIcon} title={'Community'} />
          </Link>
        </div>
      )}

      {/* <div className="icon">
        <SideBarRow Icon={WhatshotSharpIcon} title={'Trending'} />
      </div>
      <div className="icon">
        <SideBarRow Icon={SubscriptionsSharpIcon} title={'Subscribe'} />
      </div>

      <hr />
      <SideBarRow Icon={ExpandMoreOutlinedIcon} title={'Show more'} />  */}
    </div>
  );
}

export default SideBar;
