import React from 'react';
import '../OrderHistory.css';

import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
//   !style
const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
  cardGrid: {
    //     display: 'flex',
    //     justifyContent: 'center',
    //     alignItems: 'center',
  },
}));

export default function OrderHistory({ id, image, title, price, rating }) {
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <Grid container spacing={0}>
        <Grid item xs={6} className={classes.cardGrid}>
          <div className="orderHistory ">
            <img className="orderHistory__image" src={image} alt="" />

            <div className="orderHistory__info">
              <p className="orderHistory__title">{title}</p>
              <p className="orderHistory__price">
                <small>*</small>
                <strong>{price}</strong>
              </p>
              <div className="orderHistory__rating">
                {Array(rating)
                  .fill()
                  .map((_, i) => (
                    <p>⭐</p>
                  ))}
              </div>
            </div>
          </div>
        </Grid>
      </Grid>
    </div>
  );
}

