export const getBasketTotal = (basket) => basket?.reduce((amount, item) => item.price + amount, 0);
const reducer = (state, action) => {
     console.log('i am action', action);
     console.log('i am state', state);
     switch (action.type) {
          case 'ADD_TO_BASKET':
               return {
                    ...state,
                    basket: [...state.basket, action.item],
               };
          case 'ADD_TO_WISHLIST':
               return {
                    ...state,
                    wishlist: [...state.wishlist, action.item],
               };
          case 'ADD_COURSE':
               return {
                    ...state,
                    course: [...state.course, action.item],
               };

          case 'ADD_TO_HISTORY':
               return {
                    ...state,
                    basket: [...state.history, action.item],
               };

          case 'REMOVE_FROM_BASKET':
               const index = state.basket.findIndex((basketItem) => basketItem.id === action.id);
               let newBasket = [...state.basket];
               console.log(index);
               if (index >= 0) {
                    newBasket.splice(index, 1);
               } else {
                    console.warn(`
                   Can't remove product (id : ${action.id}) as it not in basket!`);
               }
               return {
                    ...state,
                    basket: newBasket,
               };
          case 'REMOVE_FROM_WISHLIST':
               const index1 = state.wishlist.findIndex((wishlistItem) => wishlistItem.id === action.id);
               let newWishList = [...state.wishlist];
               console.log(index1);
               if (index1 >= 0) {
                    newWishList.splice(index1, 1);
               } else {
                    console.warn(`
                        Can't remove product (id : ${action.id}) as it not in wistlist!`);
               }
               return {
                    ...state,
                    wishlist: newWishList,
               };

          case 'SET_USER':
               return {
                    ...state,
                    user: action.user,
               };
          default:
               return state;
     }
};

export default reducer;
