import React, { useEffect, useState } from 'react';
import { useStateValue } from '../StateProvider';
import '../TeacherDashboard.css';
import firebaseApp from '../firebase';
import TeacherCourse from './TeacherCourse';
import Add from '@material-ui/icons/Add';
import LocalLibrary from '@material-ui/icons/LocalLibrary';
import DeleteRounded from '@material-ui/icons/DeleteRounded';
import CommunitySideBar from './CommunitySideBar';
import TeacherDashboardSideBar from './TeacherDashboard/TeacherDashboardSideBar';
import { Button, Avatar } from '@material-ui/core';
import { Link } from 'react-router-dom';

export default function TeacherDashboard() {
  const [itemList, setItemList] = useState([]);
  const [title, setTitle] = useState('');
  const [id, setId] = useState('');
  const [price, setPrice] = useState();
  const [img, setImg] = useState('');
  const [rat, setRat] = useState(0);
  const [category, setCategory] = useState('');
  const [{ basket, course, user }, dispatch] = useStateValue();

  // !ADD OPERATION
  const handleAddCourse = (e) => {
    e.preventDefault();
    //     console.log('THIS IS PRICE', price + 1);
    //~ADD TO DATABASE
    if (user) {
      const ref = firebaseApp.database().ref('teacher');

      let ID = `${user.uid}${id}${title}`;
      // console.log('new id', ID);

      const temp = parseInt(price);
      //  console.log('THIS IS int price', temp + 1);
      if (title && id && category) {
        // ~ADD TO TEACHER COURSE
        ref.child(user.uid).child('course').child('item').child(ID).set({
          id: ID,
          title: title,
          image: img,
          price: temp,
          rating: rat,
          category: category,
        });
        // ~ADD TO COURSE
        const courseRef = firebaseApp.database().ref('course');
        courseRef.child('item').child(ID).set({
          id: ID,
          title: title,
          image: img,
          price: temp,
          rating: rat,
          category: category,
        });
        //    ~ADD TO CATEGORY
        const categoryRef = firebaseApp.database().ref('category');
        categoryRef.child(category).child(ID).set({
          id: ID,
          title: title,
          image: img,
          price: temp,
          rating: rat,
          category: category,
        });
        console.log('this is a category', category);
      } else {
        alert('complete form ');
      }
    } else {
      alert('Login first');
    }
  };

  // !REMOVE OPERATION
  const handleRemove = () => {
    if (user) {
      let ID = `${user.uid}${id}${title}`;
      console.log('thi sis id', ID);

      const ref = firebaseApp.database().ref('teacher');
      ref.child(user.uid).child('course').child('item').child(ID).remove();

      const courseRef = firebaseApp.database().ref('course');
      courseRef.child('item').child(ID).remove();
    } else {
      alert('Login first');
    }
  };
  return (
    <div className="teacher__dashboard">
      {/* <h2>TEACHER DASHBOARD</h2> */}
      <div className="teacher__dashboard__container">
        <h2 className="teacher__dashboard__header">TEACHER DASHBOARD</h2>
        <form className="teacher__dashboard__form">
          <h4>Add new course</h4>
          <div className="teacher__dashboard__inputContainer">
            <input
              className="teacher__dashboard__input"
              type="text"
              value={title}
              className="teacher__dashboard__input"
              placeholder="Course name"
              onChange={(e) => {
                setTitle(e.target.value);
              }}
            />
          </div>

          <div className="teacher__dashboard__inputContainer">
            <input
              className="teacher__dashboard__input"
              placeholder="ID"
              type="text"
              value={id}
              onChange={(e) => {
                setId(e.target.value);
              }}
            />
          </div>

          <div className="teacher__dashboard__inputContainer">
            <input
              className="teacher__dashboard__input"
              placeholder="Price"
              type="number"
              value={price}
              onChange={(e) => {
                setPrice(e.target.value);
              }}
            />
          </div>

          <div className="teacher__dashboard__inputContainer">
            <input
              className="teacher__dashboard__input"
              placeholder="Image"
              type="text"
              value={img}
              onChange={(e) => {
                setImg(e.target.value);
              }}
            />
          </div>

          <div className="teacher__dashboard__inputContainer">
            <select className="teacher__dashboard__input" name="cars" id="cars" onClick={(e) => setCategory(e.target.value)}>
              <option value="webdevelopment">Web Development</option>
              <option value="fullstack">Full Stack Developer</option>
              <option value="android">Android Development</option>
            </select>
          </div>
          <div>
            <Button variant="outlined" onClick={handleAddCourse} className="teacher__dashboard__form__buttons">
              Add
            </Button>

            <Link to="/teacherCourse" className="sideBar__link">
              <Button variant="outlined" className="teacher__dashboard__form__buttons">
                Remove
              </Button>
            </Link>

            {/* <button onClick={handleAddCourse}>
                <Add />
                add
              </button> */}
            {/* <button onClick={handleCheckCourse}>
                <LocalLibrary />
                my course
              </button> */}
            {/* <button onClick={handleRemove}>
                <DeleteRounded />
                remove course
              </button> */}
          </div>
        </form>
      </div>
    </div>
  );
}

